/* eslint-disable multiline-ternary */
import { Fragment, useContext, useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import toast from "react-hot-toast"
import Cleave from "cleave.js/react"
import { Check, X } from "react-feather"
import {
  Row,
  Col,
  Form,
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  Offcanvas,
  OffcanvasHeader,
  OffcanvasBody
} from "reactstrap"
import { useTranslation } from "react-i18next"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import "moment/locale/id"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { selectThemeColors, sortedObject } from "@utils"
import Select from "react-select"
import Flatpickr from "react-flatpickr"
import "@styles/react/libs/flatpickr/flatpickr.scss"
import { Indonesian } from "flatpickr/dist/l10n/id.js"
import { useForm, Controller } from "react-hook-form"
import {
  storeName,
  pageTitle,
  postData,
  putData,
  resetSelectedData,
  getDataById
} from "../store"

import { useDispatch, useSelector } from "react-redux"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"
import classnames from "classnames"
import { TextareaAutosize } from "@mui/material"
import { renderIcon } from "../store/my_menu"
import IconsFeather from "../icons/feather"
import IconsMaterial from "../icons/material"

const CustomLabel = ({ htmlFor }) => {
  return (
    <Label className="form-check-label" htmlFor={htmlFor}>
      <span className="switch-icon-left">
        <Check size={14} />
      </span>
      <span className="switch-icon-right">
        <X size={14} />
      </span>
    </Label>
  )
}

const ModalAddEdit = ({
  show,
  setShow,
  detailPage,
  formState,
  setFormState,
  currentData = null
}) => {
  const { t } = useTranslation()
  const { id } = useParams()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const { colors } = useContext(ThemeColors)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const {
    control,
    setError,
    setValue,
    handleSubmit,
    formState: { errors }
  } = useForm({})

  const [isSeparator, setIsSeparator] = useState(currentData.is_separator)
  const [iconCategory, setIconCategory] = useState("feather")
  const [icon, setIcon] = useState(currentData?.icon)

  const [canvasPlacement, setCanvasPlacement] = useState("start")
  const [canvasOpen, setCanvasOpen] = useState(false)
  const toggleCanvasStart = () => {
    setIconCategory("feather")
    setCanvasPlacement("start")
    setCanvasOpen(!canvasOpen)
  }

  const toggleCanvasBottom = () => {
    setIconCategory("feather")
    setCanvasPlacement("bottom")
    setCanvasOpen(!canvasOpen)
  }

  const countChanges = (data) => {
    // if (formState === "edit") {
    //   const storeData = { ...sortedObject(store.selectedData) }
    //   delete storeData["id"]
    //   for (let key in storeData) {
    //     if (!(key in data)) delete storeData[key]
    //   }
    //   // console.log(JSON.stringify(sortedObject(storeData)))
    //   // console.log(JSON.stringify(sortedObject(data)))
    //   return (
    //     JSON.stringify(sortedObject(storeData)) !==
    //     JSON.stringify(sortedObject(data))
    //   )
    // } else {
    //   return true
    // }

    return true
  }

  const updateDataToApi = (data) => {
    // console.log(data)
    data["is_separator"] = isSeparator
    data["icon"] = icon
    // console.log(data)
    if (data.parent_id) {
      if (data.parent_id.value >= 9000) {
        data["id_app"] = data.parent_id.id_app
        data["parent_id"] = null
      } else {
        data["parent_id"] = data.parent_id.value
      }
    }
    // console.log(data)
    if (countChanges(data)) {
      if (
        Object.values(data).every((field) => {
          return field !== null
        }) ||
        data["parent_id"] === null
      ) {
        if (!isSeparator && data["routePath"] === "") {
          for (const key in data) {
            if (data[key] !== null && data[key]?.length === 0) {
              setError(key, {
                type: "manual"
              })
            }
          }
        } else {
          setLoadingText(`${t("Updating")} ${t(pageTitle)} ${t("Detail")} Data`)
          dispatch(putData(data)).then((res) => {
            // console.log(res)
            if (res.meta.requestStatus === "fulfilled") {
              setShow(!show)
              toast.success(`${t("Successfully Updated")} ${t(pageTitle)}`, {
                style: {
                  padding: "16px",
                  color: colors.success.main,
                  border: `1px solid ${colors.success.main}`
                },
                iconTheme: {
                  primary: colors.success.main
                }
              })
              if (detailPage) {
                dispatch(getDataById(id))
              }
            } else if (res.meta.requestStatus === "rejected") {
              toast.error(res.payload.message, {
                style: {
                  padding: "16px",
                  color: colors.danger.main,
                  border: `1px solid ${colors.danger.main}`
                },
                iconTheme: {
                  primary: colors.danger.main
                }
              })
            }
          })
        }
      } else {
        for (const key in data) {
          if (data[key]?.length === 0) {
            setError(key, {
              type: "manual"
            })
          }
        }
      }
    } else {
      toast(`${t("You haven't changed anything")}`, { icon: "ðŸ‘" })
    }
  }

  const postDataToApi = (data) => {
    // console.log(data)
    if (
      Object.values(data).every((field) => {
        return field !== null
      })
    ) {
      if (!isSeparator && data["routePath"] === "") {
        for (const key in data) {
          if (data[key] !== null && data[key]?.length === 0) {
            setError(key, {
              type: "manual"
            })
          }
        }
      } else {
        setLoadingText(`${t(`Creating a New ${pageTitle}`)}`)

        // console.log(currentData, data)

        if (!currentData.is_app_header) {
          data["parent_id"] = currentData.id
        } else {
          data["parent_id"] = null
        }

        data["id_app"] = currentData.id_app
        data["is_separator"] = isSeparator
        data["icon"] = icon
        // console.log(data)

        dispatch(postData(data)).then((res) => {
          // console.log(res)
          if (res.meta.requestStatus === "fulfilled") {
            setShow(!show)
            toast.success(`${t("Successfully Created")} ${t(pageTitle)}`, {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            })
            if (detailPage) {
              dispatch(getDataById(id))
            }
          } else if (res.meta.requestStatus === "rejected") {
            toast.error(res.payload.message, {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          }
        })
      }
    } else {
      for (const key in data) {
        if (data[key] !== null && data[key]?.length === 0) {
          setError(key, {
            type: "manual"
          })
        }
      }
    }
  }

  const handleReset = () => {
    dispatch(resetSelectedData())
  }

  const changeSeparator = (data) => {
    // console.log(data.target.checked)
    setIsSeparator(data.target.checked)
    if (data.target.checked) {
    }
  }

  // const changeParentId = (data) => {
  //   console.log(data)

  //   if (data.is_app_header) {
  //     setValue("parent_id", { value: null })
  //   } else {
  //     setValue("parent_id", data)
  //   }

  //   // if (data?.routePath) {
  //   //   setValue("parent_id", data.id)
  //   //   setValue("routePath", `${data?.routePath}/`)
  //   // }
  // }

  // useEffect(() => {
  //   console.log(store.dataDropdown)
  // }, [])

  return (
    <Modal
      isOpen={show}
      toggle={() => {
        handleReset()
        setShow(!show)
      }}
      className="modal-dialog-centered modal-lg"
    >
      <UILoader
        blocking={store.isLoadingAddEdit || store.isLoadingDetail}
        loader={<Loader />}
      >
        <ModalHeader
          className="bg-transparent"
          toggle={() => {
            handleReset()
            setShow(!show)
          }}
        ></ModalHeader>
        <ModalBody className="px-sm-5 pt-50 pb-5">
          <div className="text-center mb-2">
            <h2 className="mb-1">
              {formState === "edit" ? t("Edit") : t("Add Sub")} {t(pageTitle)}
            </h2>
            {formState !== "edit" && (
              <p className="mb-1">
                {t("Child for ")} {currentData ? t(currentData.title) : ""}
              </p>
            )}
          </div>
          <Form
            onSubmit={
              formState === "edit"
                ? handleSubmit(updateDataToApi)
                : handleSubmit(postDataToApi)
            }
          >
            <Row className="gy-1 pt-75">
              <Col md={8} xs={7}>
                <Label className="form-label" for="title">
                  {t("Title")}
                  <span className="text-danger">*</span>
                </Label>
                <Controller
                  defaultValue={store.selectedData?.title ?? ""}
                  control={control}
                  id="title"
                  name="title"
                  render={({ field }) => (
                    <Input
                      {...field}
                      className={
                        errors.title
                          ? "form-control is-invalid"
                          : "form-control"
                      }
                      id="title"
                      invalid={errors.title && true}
                    />
                  )}
                />
              </Col>

              {!isSeparator && (
                <Col md={2} xs={2} className="text-center">
                  <Label className="form-label" for="icon">
                    {t("Icon")}
                    <span className="text-danger">*</span>
                  </Label>
                  <div>
                    <Button.Ripple
                      className="btn-icon"
                      outline
                      color="primary"
                      size="sm"
                      onClick={toggleCanvasBottom}
                    >
                      {renderIcon(icon, "14")}
                    </Button.Ripple>
                  </div>
                  {/* <Controller
                    defaultValue={store.selectedData?.icon ?? ""}
                    control={control}
                    id="icon"
                    name="icon"
                    render={({ field }) => (
                      <Input
                        {...field}
                        className={
                          errors.icon
                            ? "form-control is-invalid"
                            : "form-control"
                        }
                        id="icon"
                        invalid={errors.icon && true}
                      />
                    )}
                  /> */}
                </Col>
              )}

              <Col className=" d-grid justify-content-center" md={2} xs={3}>
                <Label className="form-label" for="is_separator">
                  {t("Separator")}
                </Label>
                <div
                  style={{ marginTop: "4px" }}
                  className="form-switch form-check-primary mx-auto"
                >
                  <Input
                    type="switch"
                    defaultChecked={isSeparator}
                    onChange={(data) => {
                      changeSeparator(data)
                    }}
                    id="icon-primary"
                    name="icon-primary"
                  />
                  <CustomLabel htmlFor="icon-primary" />
                </div>
              </Col>

              <Col md={6} xs={12}>
                <Label className="form-label" for="parent_id">
                  {t("Parent Menu")}
                </Label>
                <Controller
                  defaultValue={
                    store.dataDropdown.filter(
                      (el) =>
                        el.value ===
                        (formState === "edit"
                          ? currentData?.parent_id
                          : currentData?.id)
                    ) ?? ""
                  }
                  control={control}
                  id="parent_id"
                  name="parent_id"
                  render={({ field }) => (
                    <Select
                      {...field}
                      id="parent_id"
                      theme={selectThemeColors}
                      defaultValue={store.dataDropdown.filter(
                        (el) =>
                          el.value ===
                          (formState === "edit"
                            ? currentData?.parent_id
                            : currentData?.id)
                      )}
                      // onChange={(data) => {
                      //   changeParentId(data)
                      // }}
                      isDisabled={formState === "add"}
                      options={store.dataDropdown
                        .filter((el) => el.value !== store.selectedData?.id)
                        .map((item) => ({
                          ...item,
                          // id_app: item.id_app,
                          label: t(item.title)
                        }))}
                      className={classnames("react-select", {
                        "is-invalid": store.selectedData?.parent_id === null
                      })}
                      classNamePrefix="select"
                    />
                  )}
                />
              </Col>

              {!isSeparator && (
                <Col md={6} xs={12}>
                  <Label className="form-label" for="subject">
                    {t("Route Path")}
                    <span className="text-danger">*</span>
                  </Label>
                  <Controller
                    defaultValue={store.selectedData?.routePath ?? ""}
                    control={control}
                    id="routePath"
                    name="routePath"
                    render={({ field }) => (
                      <Input
                        {...field}
                        className={
                          errors.routePath
                            ? "form-control is-invalid"
                            : "form-control"
                        }
                        id="routePath"
                        invalid={errors.routePath && true}
                      />
                    )}
                  />
                </Col>
              )}

              <Col md={12} xs={12}>
                <Label className="form-label" for="description">
                  {t("Description")}
                </Label>
                <Controller
                  defaultValue={store.selectedData?.description ?? ""}
                  control={control}
                  id="description"
                  name="description"
                  render={({ field }) => (
                    <TextareaAutosize
                      {...field}
                      className="form-control"
                      id="description"
                    />
                  )}
                />
              </Col>

              <Col xs={12} className="text-center mt-2 pt-50">
                <Button type="submit" className="me-1" color="primary">
                  {t("Submit")}
                </Button>
                <Button
                  type="reset"
                  color="secondary"
                  outline
                  onClick={() => {
                    handleReset()
                    setFormState("add")
                    setShow(false)
                  }}
                >
                  {t("Cancel")}
                </Button>
              </Col>
            </Row>
          </Form>
        </ModalBody>
      </UILoader>

      <Offcanvas
        direction={canvasPlacement}
        isOpen={canvasOpen}
        toggle={toggleCanvasStart}
      >
        <OffcanvasHeader className="mb-1 mt-1 me-1" toggle={toggleCanvasStart}>
          {t("Select")} {t("Icon")}
          <Button.Ripple
            className="ms-2 me-2"
            color="primary"
            outline={iconCategory !== "feather"}
            onClick={(event) => {
              setIconCategory("feather")
            }}
          >
            Feather Icon
          </Button.Ripple>
          <Button.Ripple
            color="primary"
            outline={iconCategory !== "material"}
            onClick={(event) => {
              setIconCategory("material")
            }}
          >
            Material Icon
          </Button.Ripple>
        </OffcanvasHeader>
        <OffcanvasBody
          className={classnames({
            "my-auto mx-0 flex-grow-0":
              canvasPlacement === "start" || canvasPlacement === "end"
          })}
        >
          {iconCategory === "feather" ? (
            <IconsFeather setIcon={setIcon} setCanvasOpen={setCanvasOpen} />
          ) : (
            // <IconsFeather setIcon={setIcon} setCanvasOpen={setCanvasOpen} />
            <IconsMaterial setIcon={setIcon} setCanvasOpen={setCanvasOpen} />
          )}
          {/* <Button
            color="primary"
            onClick={toggleCanvasStart}
            className={classnames({
              "mb-1": canvasPlacement === "start" || canvasPlacement === "end",
              "me-1": canvasPlacement === "top" || canvasPlacement === "bottom"
            })}
            {...(canvasPlacement === "start" || canvasPlacement === "end"
              ? { block: true }
              : {})}
          >
            Continue
          </Button>
          <Button
            outline
            color="secondary"
            onClick={toggleCanvasStart}
            {...(canvasPlacement === "start" || canvasPlacement === "end"
              ? { block: true }
              : {})}
          >
            Cancel
          </Button> */}
        </OffcanvasBody>
      </Offcanvas>
    </Modal>
  )
}

export default ModalAddEdit
