/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-confusing-arrow */
/* eslint-disable multiline-ternary */
import {
  Fragment,
  useState,
  useEffect,
  useContext,
  useMemo,
  useCallback,
  forwardRef,
  Component
} from "react"
import { Link } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { AbilityContext } from "@src/utility/context/Can"
import ModalAddEdit from "./modal"
import { neutralizeBack, revivalBack } from "@src/utility/Utils"
import {
  storeName,
  pageTitle,
  serverSideGrid,
  detailFromGrid,
  getApiAfterPut,
  getApiAfterPost,
  getApiAfterDelete,
  getSummarysData,
  getAlwaysGrid,
  getDataList,
  getDataById,
  setDataById,
  deleteData,
  deleteSelectedRowData,
  setDataList,
  putData
} from "./store"
import { renderIcon } from "./store/my_menu"
import { getInvoice } from "../../invoices/store"
import { useDispatch, useSelector } from "react-redux"
import ReactPaginate from "react-paginate"
import DataTable from "react-data-table-component"
import debounce from "lodash.debounce"
import {
  ChevronDown,
  Share,
  Printer,
  FileText,
  File,
  Grid,
  Copy,
  Slack,
  User,
  Settings,
  Database,
  Edit2,
  MoreVertical,
  Trash2,
  Archive,
  Eye,
  CheckCircle,
  CheckSquare,
  Check,
  Square,
  Plus
} from "react-feather"

import { getUserData } from "@utils"

import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import toast from "react-hot-toast"
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  CardTitle,
  CardHeader,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown,
  Badge,
  UncontrolledTooltip,
  CardText
} from "reactstrap"
import Swal from "sweetalert2"
import withReactContent from "sweetalert2-react-content"
const MySwal = withReactContent(Swal)
import "@styles/react/libs/react-select/_react-select.scss"
import "@styles/react/libs/tables/react-dataTable-component.scss"
import SortableTree from "@nosferatu500/react-sortable-tree"
import { Loader } from "@src/shared/CommonComponents"

const List = ({
  searchString,
  lookupMode,
  lookupMode_fieldName,
  lookupMode_defaultValue,
  lookupMode_onChange,
  lookupMode_isMulti,
  lookupMode_isM2MDefValFromGrid
}) => {
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const { t } = useTranslation()
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const ability = useContext(AbilityContext)
  const [loading, setLoading] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const [showModal, setShowModal] = useState(false)
  const [formState, setFormState] = useState(null)
  const [currentDataAdd, setCurrentDataAdd] = useState(null)

  const [searchFocusIndex, setSearchFocusIndex] = useState(0)
  const [searchFoundCount, setSearchFoundCount] = useState(null)

  const { colors } = useContext(ThemeColors)

  const handleEdit = (rowData) => {
    // console.log(rowData)
    setCurrentDataAdd(rowData)
    setFormState("edit")
    if (!detailFromGrid) {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(getDataById(rowData.id)).then((res) => {
        if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        } else {
          setLoading(false)
          setShowModal(true)
        }
      })
    } else {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(setDataById(rowData)).then(() => {
        setLoading(false)
        setShowModal(true)
      })
    }
  }

  const handleDelete = async (id) => {
    const result = await MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You won't be able to revert")} ${t(pageTitle)}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete")} ${t(pageTitle)}!`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    })
    if (result.value) {
      setLoadingText(`${t("Deleting")} Data ${t(pageTitle)}`)
      setLoading(true)
      dispatch(deleteData(id)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setLoading(false)
          toast.success(`${t("Successfully deleted")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    }
  }

  const toggleSidebar = (row = null) => {
    // console.log(row)
    setCurrentDataAdd(row)
    setFormState("add")
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    neutralizeBack(handleCloseModal)
  }

  const handleCloseModal = () => {
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    revivalBack()
  }

  const handleChange = (tree) => {
    dispatch(setDataList(tree))
  }

  const handleChangeLookupMode = (
    e,
    id,
    lookupMode_fieldName,
    lookupMode_isMulti
  ) => {
    let mainArray = JSON.parse(JSON.stringify(store.data))
    mainArray.forEach(function iter(a) {
      if (a.id === id) {
        a.checked = e.target.checked
      }
      // eslint-disable-next-line no-unused-expressions
      Array.isArray(a.children) && a.children.forEach(iter)
    })
    handleChange(mainArray)
    return lookupMode_onChange(id, lookupMode_fieldName, lookupMode_isMulti)
  }

  const eventCanDragNode = (args) => {
    // console.log(args)
    if (args.node?.is_app_header) {
      return false
    }
    return true
  }

  const onMoveNode = ({
    treeData,
    node,
    nextParentNode,
    prevPath,
    prevTreeIndex,
    nextPath,
    nextTreeIndex
  }) => {
    // console.log(treeData)
    // console.log(node)
    // console.log(nextParentNode)
    // console.log(prevPath)
    // console.log(prevTreeIndex)
    // console.log(nextPath)
    // console.log(nextTreeIndex)
    const data = {
      id: node.id,
      title: node.title,
      parent_id: nextParentNode?.is_app_header ? null : nextParentNode.id,
      index: nextTreeIndex ?? 1
    }
    updateDataToApi(data, true)
  }

  const updateDataToApi = (data, notReload = false) => {
    // console.log(data)
    setLoadingText(`${t("Updating")} ${t(pageTitle)} Data`)
    if (notReload) data["notReload"] = true
    const nameMsg = data.title
    dispatch(putData(data, notReload)).then((res) => {
      // console.log(res)
      if (res.meta.requestStatus === "fulfilled") {
        toast.success(
          `${t("Successfully Updated")} ${t(pageTitle)} ${nameMsg}`,
          {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          }
        )
      } else if (res.meta.requestStatus === "rejected") {
        toast.error(res.payload.message, {
          style: {
            padding: "16px",
            color: colors.danger.main,
            border: `1px solid ${colors.danger.main}`
          },
          iconTheme: {
            primary: colors.danger.main
          }
        })
      }
    })
  }

  const customSearchMethod = ({ node, searchQuery }) => {
    const title = node.title.toLowerCase()
    const titleTranslate = t(node.title).toLowerCase()
    // console.log(searchQuery, title.toLowerCase())
    return (
      (searchQuery && title.indexOf(searchQuery) > -1) ||
      (searchQuery && titleTranslate.indexOf(searchQuery) > -1)
    )
  }

  const selectPrevMatch = () =>
    setSearchFocusIndex(
      searchFocusIndex !== null
        ? (searchFoundCount + searchFocusIndex - 1) % searchFoundCount
        : searchFoundCount - 1
    )

  const selectNextMatch = () =>
    setSearchFocusIndex(
      searchFocusIndex !== null ? (searchFocusIndex + 1) % searchFoundCount : 0
    )

  const removeDefaultValue = (data, dataTree) => {
    // console.log(dataTree)
    let mainArray = JSON.parse(JSON.stringify(dataTree))
    mainArray.forEach(function iter(a) {
      if (data.includes(a.id)) {
        a["checked"] = true
        a["hidden"] = true
      }
      // console.log(a.id, !data.includes(a.id))

      // eslint-disable-next-line no-unused-expressions
      Array.isArray(a.children) && a.children.forEach(iter)
    })
    // console.log(data)
    // console.log(mainArray)
    dispatch(setDataList(mainArray))
  }

  useEffect(() => {
    if (store.data.length === 0 || (!store.isLoading && getAlwaysGrid)) {
      setLoadingText(`${t("Loading Data")} ${t(pageTitle)}`)
      dispatch(getDataList({})).then((x) => {
        // console.log(lookupMode_isM2MDefValFromGrid)
        if (lookupMode && lookupMode_isM2MDefValFromGrid) {
          removeDefaultValue(lookupMode_defaultValue, x.payload.data)
        }
      })
    }
    // console.log(lookupMode_isM2MDefValFromGrid)
    if (store.data.length > 0 && lookupMode_isM2MDefValFromGrid && lookupMode) {
      removeDefaultValue(lookupMode_defaultValue, store.data)
    }
  }, [])

  return (
    <Fragment>
      <UILoader blocking={store.isLoading} loader={<Loader />}>
        <Card className="overflow-hidden">
          <div className="react-dataTable react-dataTable-selectable-rows">
            <div
              style={{
                height: !lookupMode ? 800 : 350,
                margin: !lookupMode ? 20 : 10
              }}
            >
              <SortableTree
                treeData={store.data}
                onChange={handleChange}
                onMoveNode={!lookupMode ? onMoveNode : null}
                canDrag={!lookupMode ? eventCanDragNode : false}
                searchMethod={customSearchMethod}
                searchQuery={searchString}
                searchFocusOffset={searchFocusIndex}
                searchFinishCallback={(matches) => {
                  setSearchFoundCount(matches.length)
                  setSearchFocusIndex(
                    matches.length > 0 ? searchFocusIndex % matches.length : 0
                  )
                }}
                // onlyExpandSearchedNodes
                maxDepth={4}
                generateNodeProps={({ node, path }) => ({
                  style: {
                    fontSize: node.is_app_header ? `16px` : "14px",
                    fontWeight: node.is_app_header ? `bold` : "normal"
                  },
                  title: (
                    <Fragment>
                      {!node.is_app_header ? (
                        !node.is_separator ? (
                          node.icon ? (
                            <div>{renderIcon(node.icon, "", 24)} </div>
                          ) : (
                            <div
                              style={{
                                fontWeight: node.is_app_header ? `bold` : 400
                              }}
                            >
                              {t(node.title)}
                            </div>
                          )
                        ) : null
                      ) : (
                        <img
                          className="me-1"
                          src={node.logo}
                          width="30"
                          height="25"
                        />
                      )}
                      <div
                        style={{
                          paddingLeft: node.is_app_header ? 0 : 10,
                          fontWeight:
                            node.is_app_header || node.is_separator
                              ? `bold`
                              : 400,
                          textTransform: node.is_separator ? `uppercase` : "",
                          color: node.is_separator ? "#a6a4b0" : "inherit"
                        }}
                      >
                        {t(node.title)}
                      </div>
                    </Fragment>
                  ),
                  buttons: [
                    ability.can(
                      "post",
                      sessionStorage.getItem("current_page_perm")
                    ) &&
                    !lookupMode &&
                    !node.is_separator ? (
                      <>
                        <Button.Ripple
                          className="btn-icon rounded-circle"
                          color="flat-primary"
                          id="btn-edit"
                          onClick={(e) => {
                            toggleSidebar(node)
                          }}
                        >
                          <Plus size={16} />
                        </Button.Ripple>
                        <UncontrolledTooltip placement="left" target="btn-edit">
                          {t("Add Child")}
                        </UncontrolledTooltip>
                      </>
                    ) : null,
                    ability.can(
                      "update",
                      sessionStorage.getItem("current_page_perm")
                    ) &&
                    !lookupMode &&
                    !node.is_app_header ? (
                      <>
                        <Button.Ripple
                          className="btn-icon rounded-circle"
                          color="flat-info"
                          id="btn-edit"
                          onClick={(e) => {
                            e.preventDefault()
                            handleEdit(node)
                          }}
                        >
                          <Edit2 size={16} />
                        </Button.Ripple>
                        <UncontrolledTooltip placement="left" target="btn-edit">
                          {t("Edit")}
                        </UncontrolledTooltip>
                      </>
                    ) : null,
                    ability.can(
                      "delete",
                      sessionStorage.getItem("current_page_perm")
                    ) &&
                    !lookupMode &&
                    !node.is_app_header ? (
                      <>
                        <Button.Ripple
                          id="btn-delete"
                          className="btn-icon rounded-circle"
                          color="flat-danger"
                          onClick={(e) => {
                            e.preventDefault()
                            handleDelete(node.id)
                          }}
                        >
                          <Trash2 size={16} />
                        </Button.Ripple>
                        <UncontrolledTooltip
                          placement="left"
                          target="btn-delete"
                        >
                          {t("Delete")}
                        </UncontrolledTooltip>
                      </>
                    ) : null,
                    lookupMode && !node.is_app_header ? (
                      <div
                        className={
                          node.hidden
                            ? "form-check form-check-secondary"
                            : "form-check form-check-primary"
                        }
                      >
                        <Input
                          disabled={node.hidden}
                          defaultChecked={node.checked}
                          onChange={(e) =>
                            handleChangeLookupMode(
                              e,
                              node.id,
                              lookupMode_fieldName,
                              lookupMode_isMulti
                            )
                          }
                          type="checkbox"
                          id={`checkbox-${node.id}`}
                        />
                      </div>
                    ) : null
                  ]
                })}
              />
            </div>
          </div>
        </Card>
      </UILoader>

      {showModal && (
        <ModalAddEdit
          formState={formState}
          show={showModal}
          setShow={toggleSidebar}
          detailPage={false}
          setFormState={setFormState}
          currentData={currentDataAdd}
        />
      )}
    </Fragment>
  )
}

export default List
